
import { defineComponent, reactive, toRefs, watchEffect, computed } from "vue";
export default defineComponent({
    name: "introduce",
    props: {
        inf: {
            type: Object,
            default: () => {
                return {};
            },
        },
    },
    setup(props) {
        const info = reactive<{ information: object }>({
            information: {},
        });

        watchEffect((): void => {
            info.information = Object.assign({}, props.inf);
        });

        return { ...toRefs(info) };
    },
});
