<div class="video-area"><div class="area-list" v-for="(item,index) in list.list" :key="index"><div class="title mb-15">{{item.name}}</div><div class="line mb-15"></div><div class="desc mb-35">{{item.info}}</div><div class="item mb-40" v-for="(ele,index) in item.list" :key="index"><video :poster="ele.cover" :src="ele.video" controls="controls" webkit-playsinline="true" x-webkit-airplay="true" x5-video-player-type="h5" x5-video-orientation="landscape" x5-video-player-fullscreen="true" x5-playsinline="true" playsinline="true"></video><div class="content">        <div class="name mb-20" v-if="videoType === 'teacher'">{{ele.teacher}}</div><div class="position mb-25" v-if="videoType === 'teacher'">{{ele.teacher_info}}</div><div class="lines mb-30" v-if="videoType === 'teacher'"></div><p class="mb-30">{{ele.info}}</p><button @click="handleUp(ele.title,ele.info)">展开全部 </button></div></div></div><look-more :show="dialog" @close="dialog = false" :inf="popup"></look-more></div>