<div class="introduce" :style="{padding : information.type === 'text' ? '0.73rem 0.5rem 0.45rem 0.5' : '0.73rem 0 0.45rem 0'}"><div class="title mb-60" :style="{width:information.width}"><img src="@/assets/images/double-line.png"><span>{{information.title}}</span><img src="@/assets/images/double-line.png"></div><div class="text" v-if="information.type === 'text'"><div class="desc mb-35" v-html="information.desc"></div><img class="icon" src="@/assets/images/down-arrow.png"></div><div class="teacher-list" v-else><div class="item" v-for="(item,index) in information.desc" :key="index"><van-image class="avatar" width="1.6rem" height="1.6rem" fit="cover" round :src="item.avatar"></van-image><div class="name mb-20">{{item.title}}</div><div class="line"></div><div class="position">{{item.introduce}}</div></div></div></div>